html, body, .root {
  height: 100%;
}
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  line-height: 1.5;
}
body, .App {
  background: #f5f5f5;
}
.App{
  position: absolute;
  width: 100%;
  height: 100%;
}
.App {
  text-align: left;
  display: -ms-flexbox;
  display: flex;
}

.available-locations{
height: 100%;
width: 250px;
background-color: black;
z-index: 4;
position: fixed;
}
h2{
  color: darkgray;
  padding-left: 10px;
}
.filter-btn{
  color : whitesmoke;
  background-color: lightseagreen;
  font-size: 15px;
  vertical-align: middle;
}
.filter-input{
  height : 27px;
  width: 246px;
}
#map{
  position: absolute;
  height:100%;
  width:100%;
  left: 0;
  top:50px;
}
.wrapper{
 
  width:650%;
  left:0;
  z-index: 3;
  background-color: black;
  height: 50px;
  position: absolute;
}
.hamburger{
    
    z-index: 2;
    position: absolute;
    left: 244px;
    color: lightseagreen;
    background-color: transparent;
    font-size: 30px;
    cursor: pointer;
    border: none;

    
}
.hidden{
width: 250px;
left:-250px;
position: fixed;
}

.search-locations-bar{
  position: absolute;
  z-index: 4;

}

.town{
  color:  lightseagreen;
  position: absolute;
  left: 280px;
  padding-top: 10px;
  font-size: 17px;
  font-weight: bold;
}
.filter-name{
  vertical-align: top;
  font-weight: bold;  
}
.Filter-result{
  color: azure;
  text-align: left;
  position: absolute;
  top: 100px;
}
.link{
  color: azure;
  text-decoration: none;
}
ul{
  list-style-type: none;
  padding-left: 0px;
}
i{
  color:tomato;
}
.content{
  display: -ms-flexbox;
  display: flex;
  max-width: 350px;
  /* overflow-wrap: break-word; */
 
}


@media screen and (max-width: 450px){
  h2{
    font-size: 20px;
  }
  .filter-result{
    font-style: italic;
    font-size: 10px;
  }
.available-locations{
  width:200px;
  }
  .filter-input{
    width: 196px;
  }
  .hidden{
    width: 200px;
    left:-200px;
    }
  .hamburger{
    left:194px;
  }
  .town{
    left:230px;
  }
}


